.modal {
    font-size: 12px;
    background-color: aliceblue;
    width: 80vh;
    height: auto;
}

.modal>.header {
    width: 90%;
    border-bottom: 1px solid gray;
    font-size: 18px;
    text-align: center;
    padding: 5px;
    margin: auto;
}

.modal>.edit-form {
    width: 90%;
    padding: 10px 5px;
    margin: auto;
    text-align: center;
    flex: 1;
    
}

.modal>.edit-input {
    margin: 1vh;
    padding: 1vh;
}
.modal>.edit-subinput {
    margin: 1vh;
    padding: 1vh;
    display: inline-flex;
}

.modal>.edit-form>.actions {
    width: 90%;
    padding: 10px 5px;
    margin: auto;
    text-align: center;
}

.modal>.close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 18px;
    border: 1px solid #cfcece;
}

.button {
    margin-left: 1vh;
    margin-right: 1vh;
    padding: 3px;
}