.input-form {
    display : inline-block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.input-label {
    color: white;
    font-size: medium;
    text-align: center;
    margin: 1vh;
    vertical-align: middle;
}

.input-err {
    color: red;
    font-size: sm;
    margin-top: 1vh;
    min-height: 2vh;
    font-weight: bold;
}
.textinput {
    background-color: white;
    font-family: sans-serif;
    min-height: 1vh;
    border: 1px dashed #aaa;
    padding: 1vh;
    margin-left: 3vh;
    resize: none;
    font-size: small;
    
    
}

/* 32 10 128 */

.textinput.hex {
    width: 32ch; /*2^128-1 hex char length*/
}

.textinput.dec {
    width: 39ch; /*2^128-1 dec char length*/
}

.textinput.bin {
    width: 128ch; /*2^128-1 bin char length*/
}



.division-symbol {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    text-align: center; 
    font-size: x-large; 
    font-weight: bold;
    margin-right: 5px;
    margin-left: 5px;
    width: 10px;
    /* margin-left: auto;
    margin-right: "auto"; */
}


