.textinput {
    background-color: white;
    font-family: sans-serif;
    min-height: 1vh;
    border: 1px dashed #aaa;
    padding: 1vh;
    margin-left: 3vh;
    resize: none;
    font-size: small;
    
    
}

/* 32 10 128 */

.textinput.hex {
    width: 32ch; /*2^128-1 hex char length*/
}

.textinput.dec {
    width: 39ch; /*2^128-1 dec char length*/
}

.textinput.bin {
    width: 128ch; /*2^128-1 bin char length*/
}

